.register-modal-box {
  width: 551px;
  height: 432px;
  background: #1D3658;
  border: 1px solid #203B60;
  border-radius: 10px;

  .register-modal-header {
    width: 100%;
    height: 62px;
    padding: 0 24px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: #E0ECFF;
    font-size: 18px;
    font-weight: 600;
    border-bottom: 1px solid rgba(147, 161, 184, 0.5);
    img{
      cursor: pointer;
    }
  }

  .register-modal-info {
    padding: 0 24px;
    margin-top: 32px;
    font-weight: 500;
    font-size: 20px;
    color: #E0ECFF;

    input {
      width: 100%;
      height: 72px;
      border: 1px solid rgba(147, 161, 184, 0.5);
      border-radius: 4px;
      background-color: transparent;
      padding-left: 24px;
      font-size: 20px;
      font-weight: 500;
      outline: none;
      color: #E0ECFF;
    }
    .register-title{
      margin-top: 17px;
      margin-bottom: 12px;
    }
    .confirm-btn{
      margin-top: 35px;
    }
    .noLegitimateText{
      margin-bottom: 4px;
    }
  }
}