.rebate-box {
  display: flex;
  align-items: center;
  justify-content: center;
  border-top: 1px solid #203b60;

  .rebate-box-info {
    width: 1145px;
    margin-top: 112px;

    .rebate-box-info-titile {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: 45px;
      font-size: 32px;
      color: #fff;

      .rebate-des {
        font-size: 14px;
        color: #93a1b8;
      }

    }

    .rebate-box-header {
      width: 100%;
      height: 200px;
      display: flex;
      align-items: center;
      padding: 0 36px;
      border: 1px solid #203B60;
      position: relative;
      background: linear-gradient(106.26deg, rgba(97, 122, 255, 0.1) 1.22%, rgba(223, 229, 255, 0) 51.54%, rgba(85, 91, 237, 0.05) 100.82%);

      .rebate-avatar {
        position: relative;

        .explained-logo {
          position: absolute;
          right: -10px;
          top: 0;
        }
      }

      .rebate-account-reward-info {
        font-weight: 500;
        font-size: 20px;
        color: #93A1B8;
        margin-left: 36px;

        .rebate-reward {
          margin-bottom: 36px;
          display: flex;
          align-items: center;
          cursor: pointer;

          .reward-btn {
            display: flex;
            width: 100px;
            height: 32px;
            align-items: center;
            justify-content: flex-end;
            padding: 0 8px;
            background: #5577FD;
            border-radius: 80.6452px;
            margin: 0 8px;
            color: #E0ECFF;

            img {

              margin-left: 12px;
            }
          }

          .no-reward {
            background: rgb(85, 119, 253);
            opacity: 0.3;
            cursor: no-drop;
          }
        }

        .rebate-code {
          display: flex;
          align-items: center;

          .no-code-hash {
            background: #5577FD;
            border-radius: 4px;
            width: 232px;
            height: 40px;
            font-weight: 700;
            font-size: 20px;
            color: #E0ECFF;
            margin-left: 8px;
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
          }

          .no-recruiter-code-hash {
            width: 120px;
            height: 40px;
            background: #5577FD;
            border-radius: 4px;
            color: #E0ECFF;
            margin-left: 8px;
            display: flex;
            align-items: center;
            font-size: 16px;
            justify-content: center;

            img {
              margin-left: 8px;
            }
          }

          .code-hash {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 160px;
            height: 32px;
            cursor: pointer;
            border-radius: 6px;
            font-size: 20px;
            color: #E0ECFF;
            margin-left: 24px;
            background-image: url('./img/code-borde.svg');
            background-size: 100% 100%;

            img {
              margin-left: 12px;
            }


          }

          .code-hash-copy {
            display: flex;

            .copied-box {
              display: flex;
              align-items: center;
              justify-content: center;
              font-size: 14px;
              color: #FFFFFF;
              margin-left: 12px;

              img {
                margin-right: 4px;
              }
            }
          }
        }
      }

      .broker-recruiter {
        position: absolute;
        right: 36px;
        top: 25px;
        width: 139px;
        height: 32px;
        background: linear-gradient(270deg, #203B60 68.12%, rgba(32, 59, 96, 0) 117.79%);
        border-radius: 1000px;

        .broker-type,
        .recruiter-type {
          width: 97px;
          height: 32px;
          display: flex;
          align-items: center;
          justify-content: center;
          justify-content: center;
          background: radial-gradient(83.25% 291.37% at 32.14% 100%, #CD7A37 0%, #FFB77D 100%);
          box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.15);
          border-radius: 1000px;
          font-weight: 600;
          font-size: 12px;
          color: #FFFFFF;
          cursor: pointer;
        }

        .recruiter-type {
          background: radial-gradient(83.25% 291.37% at 32.14% 100%, #59AE99 0%, #92FFE4 100%);
          position: absolute;
          right: 0;
          text-align: center;
        }
      }
    }

    .rebate-box-use-info {
      width: 100%;
      height: 320px;
      border: 1px solid #203B60;
      background: linear-gradient(106.26deg, rgba(97, 122, 255, 0.1) 1.22%, rgba(223, 229, 255, 0) 51.54%, rgba(85, 91, 237, 0.05) 100.82%);
      margin-top: 25px;
      padding: 52px 46px 70px 46px;

      .rebate-box-user-info-header {
        display: flex;
        align-items: center;
        justify-content: space-between;

        .rebate-box-user-info-title {
          font-weight: 500;
          font-size: 24px;
          color: #E0ECFF;

          span {
            font-size: 14px;
            color: #93A1B8;
          }
        }

        .wallet-account-box,
        .acc-wrapper {
          width: 192px;
          height: 48px;
          border: 1px solid #203B60;
          border-radius: 4px;
          display: flex;
          align-items: center;
          justify-content: center;
          background: transparent;
          font-weight: 700;
          font-size: 16px;
          color: #E0ECFF;

          img {
            margin-right: 12px;
          }
        }

        .acc-wrapper:hover {
          background: transparent;
        }
      }

      .rebate-box-use-info-box {
        width: 1052px;
        height: 96px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 74px;

        .total-users-bg,
        .total-fee-bg,
        .total-rewards-bg {
          width: 336px;
          height: 96px;
          border-radius: 4px;
          display: flex;
          align-items: center;
          justify-content: center;
          background: linear-gradient(255.96deg, #CD7A37 0%, #E0ECFF 49.48%, #5577FD 100%);

          .total-users-box,
          .total-fee-box,
          .total-rewards-box {
            width: 334px;
            height: 94px;
            border-radius: 4px;
            background-color: #0A172B;
            background-image: url('./img/total-users.png');
            background-size: 100% 100%;
            display: flex;
            align-items: center;
            padding-left: 24px;

            img {
              margin-right: 14px;
            }

            .total-users-box-info,
            .total-fee-box-info,
            .total-rewards-box-info {
              font-size: 24px;
              color: #E0ECFF;

              .total-users-box-info-title,
              .total-fee-box-info-title,
              .total-rewards-box-info-title {
                font-size: 20px;
                color: #93A1B8;
              }
            }
          }

          .total-rewards-box {
            background-image: url('./img/total-fee.png');
          }

          .total-fee-box {
            background-image: url('./img/total-rewards.png');
          }
        }
      }
    }

    .rebate-box-explained {
      width: 100%;
      height: 540px;
      border: 1px solid #203B60;
      margin-top: 25px;
      padding: 44px;
      margin-bottom: 78px;
      background: linear-gradient(106.26deg, rgba(97, 122, 255, 0.1) 1.22%, rgba(223, 229, 255, 0) 51.54%, rgba(85, 91, 237, 0.05) 100.82%);

      .rebate-box-explained-title {
        color: #E0ECFF;
        font-size: 20px;
      }

      .rebate-box-explained-list {
        width: 100%;
        height: 100%;

        .rebate-box-explained-list-box {
          display: flex;
          align-items: center;
          justify-content: space-between;
          margin-top: 30px;

          .explained-box {
            width: 512px;
            height: 104px;
            display: flex;
            align-items: center;
            justify-content: center;
            background: linear-gradient(255.96deg, #CD7A37 0%, #E0ECFF 49.48%, #5577FD 100%);
            border-radius: 4px;

            .explained-box-info {
              width: 510px;
              height: 102px;
              display: flex;
              align-items: center;
              border-radius: 4px;
              padding: 0 24px;
              font-weight: 500;

              .explained-text {
                margin-left: 24px;

                .explained-rate {
                  font-size: 20px;
                  color: #E0ECFF;
                }
              }
            }

            .explained-one {
              background-color: #233044;
            }

            .explained-two {
              background-color: #1c273d;
            }

            .explained-three {
              background-color: #142342;
            }

            .explained-four {
              background-color: #1a222f;
            }

            .explained-five {
              background-color: #1d212c;
            }

            .explained-six {
              background-color: #262132;
            }
          }
        }

        .rebate-explained-margin-box {
          margin-top: 51px;
        }
      }
    }

  }
}